import Character from '@/components/Character.vue';
import CanvasLine from '@/components/CanvasLine.vue';
import mixin from '@/common/mixin.js';
import Popups from '@/components/Popups.vue';
import Recom from '@/components/Recom/main.vue';
export default {
	components: {
		CanvasLine,
		Character,
		Popups,
		Recom
	},
	data(){
		return {
			isMobile:false,
			mobile:'',
			order_sn:''
		}
	},
	onBackPress(){
		uni.switchTab({
			url: '/pages/index/ceming'
		});
		return true;
	},
	onNavigationBarButtonTap(obj){
		if(obj.type=="home"){
			uni.switchTab({
				url:'/pages/user/index'
			})
		}
	},
	methods:{
		addCharacter(){
			this.$refs.character.show();
		},
		openMobile(){
			this.$refs.phone.show();
		},
		saveMobile(){
			if(this.mobile.length!=11){
				uni.showToast({
					title:'手机号格式不正确',
					icon: 'none'
				})
				return;
			}
			this.$http.post('/v5/order/send',{
				mobile:this.mobile,
				order_sn:this.order_sn
			}).then(res=>{
				if(res.code==1){
					this.isMobile=false;
					this.$refs.phone.hide();
					uni.showToast({
						title:'发送成功',
						icon: 'none'
					})
				}
			})
		}
	},
	mixins: [mixin]
}
